.blob-button {
  z-index: 1;
  text-align: center;
  color: #fff;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-radius: 25px;
  outline: none;
  font-family: Caveat Brush, sans-serif;
  font-size: 18px;
  line-height: 1.27;
  transition: transform .2s, color .5s;
  position: relative;
  box-shadow: 0 6px 6px rgba(171, 129, 108, .25);
}

.blob-button:before {
  content: "";
  z-index: 1;
  width: 100%;
  height: 100%;
  border: 2px solid #ab816c;
  border-radius: 25px;
  position: absolute;
  top: 0;
  left: 0;
}

.blob-button:after {
  content: "";
  z-index: -2;
  width: 100%;
  height: 100%;
  border-radius: 25px;
  transition: all .3s .2s;
  position: absolute;
  top: 3px;
  left: 3px;
}

.blob-button:hover {
  color: #ab816c;
  border-radius: 25px;
  transform: translate(0, 1px);
  box-shadow: 0 5px 5px rgba(171, 129, 108, .25);
}

.blob-button:hover:after {
  border-radius: 25px;
  transition: all .3s;
  top: 0;
  left: 0;
}

.blob-button:active {
  transform: translate(1px, 3px);
  box-shadow: 0 4px 4px rgba(171, 129, 108, .25);
}

.blob-button__inner {
  z-index: -1;
  width: 100%;
  height: 100%;
  background: #ab816c;
  border-radius: 25px;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}

.blob-button__blobs {
  height: 100%;
  filter: url("#goo");
  display: block;
  position: relative;
}

.blob-button__blob {
  width: 25%;
  height: 100%;
  background: #fff;
  border-radius: 100%;
  transition: transform .45s;
  position: absolute;
  top: 2px;
  transform: translate3d(0, 150%, 0)scale(1.7);
}

@supports (filter: url("#goo")) {
  .blob-button__blob {
    transform: translate3d(0, 150%, 0)scale(1.4);
  }
}

.blob-button__blob:nth-child(1) {
  transition-delay: 0s;
  left: 0%;
}

.blob-button__blob:nth-child(2) {
  transition-delay: 80ms;
  left: 30%;
}

.blob-button__blob:nth-child(3) {
  transition-delay: .16s;
  left: 60%;
}

.blob-button__blob:nth-child(4) {
  transition-delay: .24s;
  left: 90%;
}

.blob-button:hover .blob-button__blob {
  transform: translateZ(0)scale(1.7);
}

@supports (filter: url("#goo")) {
  .blob-button:hover .blob-button__blob {
    transform: translateZ(0)scale(1.4);
  }
}

